.Icon {
    display: flex;
    align-items: flex-end;
}

.Icon .iconImage {
    width: 70px;
    margin-right: 10px;
}

.Icon .iconDescription {
    font-weight: 500;
    color: #fff;
    font-size: 1.1rem;
}

@media screen and (max-width: 768px) {
    .Icon .iconImage {
        width: 60px;
    }
}